import { t } from '../locale';

export default {
  options: {
    styleIsolation: 'shared',
  },
  props: {
    title: { type: String, default: '' },
    showToolbar: Boolean,
    cancelButtonText: {
      type: String,
      default: t('picker.cancel'),
    },
    confirmButtonText: {
      type: String,
      default: t('picker.confirm'),
    },
    customClass: {
      type: String,
      default: '',
    },
  },
  methods: {
    emit(...args) {
      this.$emit('emit', ...args);
    },
  },
};
